import Toast from './toast.vue'

let index = {}

index.install = function(Vue) {
  const VueToast = Vue.extend(Toast) //
  let toast = null

  // 3. 
  Vue.mixin({
    watch: {
      $route() {
        if (toast && toast.closeOnHashChange) {
          toast && toast.close()
        }
      }
    }
  })

  // 4. 
  Vue.prototype.$JdkToast = params => {
    if (!toast) {
      toast = new VueToast().$mount() //
      document.body.appendChild(toast.$el) //
    }
    toast.show(params)
    // 
    return toast
  }
}

export default index
